<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>修改文章</span>
    </div>
    <div class="text item">
      <el-form ref="searchForm" :model="searchForm" :rules="rules" label-width="100px">
        <el-form-item label="标题：" prop="title">
          <el-input v-model="searchForm.title" placeholder="请输入内容 " style="width:200px;"></el-input>
        </el-form-item>
        <el-form-item label="内容：" prop="content">
          <quillEditor v-model="searchForm.content"></quillEditor>
        </el-form-item>
        <el-form-item label="封面：" prop="cover">
          <el-radio v-model="searchForm.cover.type" label="1">单图</el-radio>
          <el-radio v-model="searchForm.cover.type" label="3">三图</el-radio>
          <el-radio v-model="searchForm.cover.type" label="0">无图</el-radio>
          <el-radio v-model="searchForm.cover.type" label="-1">自动</el-radio>
        </el-form-item>
        <!-- <el-form-item label="频道：" prop="channel_id">
          <el-select v-model="searchForm.channel_id" placeholder="请选择">
            <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item> -->
        <pindao :son="searchForm.channel_id" @slt="getSonId"></pindao>
        <el-form-item>
          <el-button type="success" icon="el-icon-check" circle @click="push(false)"></el-button>
          <!-- <el-button type="primary" icon="el-icon-edit" circle @click="rough"></el-button> -->
        </el-form-item>
      </el-form>
    </div>
  </el-card>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import pindao from '@/components/pindao.vue'
export default {
  name: 'articleAdd',
  data () {
    return {
      options: null,
      searchForm: {
        title: '',
        cover: {
          images: [],
          type: 0
        },
        channel_id: '',
        content: ''
      },
      rules: {
        title: [
          { required: true, message: '请输入活动名称', trigger: 'blur' },
          { min: 5, max: 30, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '请选择频道', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '请填写内容信息', trigger: 'blur' }
        ]
      }
    }
  },
  components: {
    quillEditor,
    pindao
  },
  methods: {
    getSonId (val) {
      this.searchForm.channel_id = val
    },
    // 保存修改信息
    push (flag) {
      this.$refs.searchForm.validate(valid => {
        if (valid) {
          this.$http
            .put(`/articles/${this.getId}`, this.searchForm, {
              params: { draft: flag }
            })
            .then(result => {
              this.$router.push('/article')
              this.$message({
                message: '恭喜你，修改文章成功',
                type: 'success'
              })
            })
            .catch(err => {
              console.log(err)
            })
        } else {
        //   console.log('error submit!!')
          return false
        }
      })
    },
    // 获取即将修改的文章信息
    getAmend () {
      this.$http.get(`/articles/${this.getId}`)
        .then(result => {
        //   console.log(result.data.data)
          this.searchForm = result.data.data
        })
        .catch(() => {
          alert('网路开小差，请稍后重试')
        })
    }
  },
  created () {
    this.getAmend()
    // 获取首屏频道数据
    // this.$http
    //   .get('/channels')
    //   .then(result => {
    //     this.options = result.data.data.channels
    //   })
    //   .catch(error => {
    //     console.log(error)
    //   })
  },
  computed: {
    // 获取文章信息
    getId () {
      return this.$route.params.mid
    }
  }
}
</script>

<style lang="less" scoped>
.quill-editor/deep/ .ql-container {
  height: 400px;
}
</style>
